<template lang="html">
  <div class="login">
<van-nav-bar
  title="客服"
  left-text=""
  left-arrow
  @click-left="$router.go(-1)"
/>

<div class="im" id="im_mess" ref="im_mess" @scroll="onScroll">
  <ul>
    <li v-if="$store.state.im.recive.length==0 && !$store.state.im.recive.isloading">暂无通讯消息。。。</li>
    <li v-if="$store.state.im.recive.isloading">Loading。。。</li>
  <template v-for="(k,i) in $store.state.im.recive">
    <li :class="[k.from==$store.state.im.id?'':'chatmine']">
      <div class="msg_top left">
        <img class="im_pic" v-if="$store.state.im.user[k.from+'']" :src="$store.state.im.user[k.from+'']['pic']">
      </div>
      <div class="msg_text">
        <div class="username">
          <span v-if="$store.state.im.user[k.from+'']">{{$store.state.im.user[k.from+'']['name']}}</span>
          <span>{{k.content['time']}}</span>
          </div>
        <div class="msg_text_text" v-html="ImTxt(k.content.text)"></div>
      </div>
      
    </li>
  </template>
  </ul>
</div>

<div class="msginput" >
<van-cell-group>
  <van-field
    center
    v-model="message"
    label=""
    placeholder="请输入消息内容"
  >
    <van-button slot="button" size="small" style="height:40px;" type="primary" @click="SendMess">发送</van-button>
  </van-field>
</van-cell-group>
</div>


  </div>
</template>

<script>
import { Button,Field,CellGroup,NavBar,Toast} from 'vant';
import 'vant/lib/vant-css/index.css';
import Im from '@/util/im'

export default {
  components:{
    [Button.name]:Button,
    [Field.name]:Field,
    [CellGroup.name]:CellGroup,
    [NavBar.name]:NavBar,
  },
  data(){
    return {
      message:'',
      page:0,
      isloading:false,
      clientHeight: '100px',
    }
  },
  updated:function(){
    this.ToEnd();
  },
  watch:{
    clientHeight(){
      this.ToEnd();
    }
  },
  mounted()
  {
    this.clientHeight = `${document.documentElement.clientHeight}px`;
    const that = this;
    window.onresize = function temp() {
        that.clientHeight = `${document.documentElement.clientHeight}px`;
    };
    this.ToEnd();
    //window.addEventListener('scroll', this.handleScroll)
    //发送已读qos
    Im.SendReciveQos(this);
  },
  destroyed () {
    //window.removeEventListener('scroll', this.handleScroll)
  },
  methods:{
    ImTxt(a){
      return Im.ImTxt(a);
    },
    ToEnd(){
      this.$nextTick(function(){
      var div = document.getElementById('im_mess');
      div.scrollTop = div.scrollHeight;
      })
    },
    onScroll () {
      let scrollTop=this.$refs.im_mess.scrollTop;
      if(scrollTop<150 && !this.$store.state.im.isloading && !this.$store.state.im.isfinished)
      {
        this.$store.state.im.isloading=true;
        Im.SendChatList(this,this.$conf.ChuanHai_ServieID,'friend',this.$store.state.im.page+1);
      }
      //console.log(scrollTop)
      //this
    },
    SendMess(){
      if(this.message)
      {
        Im.SendMess(this,this.message);
        this.message='';
      }
      else{Toast('请输入消息内容！')}
      
    }
  }
}

</script>

<style lang="less" scoped>
.im {
  font-size: 16px;
  height:calc(~"100vh - 46px - 70px");
  height:-webkit-calc(~"100vh - 46px - 70px"); 
  height:-moz-calc(~"100vh - 46px - 70px"); 
  overflow-y:auto;
  //background-color:rgba(117, 107, 107, 0.199);
  padding-top: 0vh;padding-left:1vw;padding-right:1vw;
  padding-bottom: 10px;
  //bottom:10vh;
  li{
    display: flex;
    justify-content: flex-start;
    padding-top:15px;
    }
}
.username
{
  line-height: 24px;
  font-size: 12px;
  white-space: nowrap;
  color: #999;
  text-align: left;
  font-style: normal; 
}
.im_pic{width:55px;height:55px;border-radius:100%;}
.msg_top{width:60px;height:100%;}
.msginput{
  //position: fixed;
  bottom:0px;height:60px;overflow:hidden;
}
.msg_text_text
{    
  position: relative;
  min-height: 22px;
  line-height: 22px;
  margin-top: 10px;
  padding: 8px 15px;
  background-color: #eee;
  border-radius: 3px;
  color: #333;
  word-break: break-all;
 }
.msg_text_text2
{    
  position: relative;
  min-height: 22px;
  line-height: 22px;
  margin-top: 10px;
  padding: 8px 15px;
  background-color: #5FB878;
  border-radius: 3px;
  color: #333;
  word-break: break-all;
 }
.msg_text{padding-left:1vw;}
.left{float: left;}
span{display:-moz-inline-box;display:inline-block;}
.chatmine
{
  //position:absolute;
  .msg_text_text{background-color:#5FB878;}
}
</style>



